import X from 'src/component/X';

export default function Blog({title}) {
    return (
        <>
            <X.Title>{title}</X.Title>
            <X.P></X.P>
            <X.CodeBlock
                language="python"
                code={`
                import numpy
                import torch

                #numpy
                arr=numpy.array([
                    [1,4,5,7,8],
                    [9,1,2,8,4],
                    [8,5,1,3,6],
                    [3,2,4,6,5],
                ])

                #torch
                tsr=torch.tensor(arr)
                `}
            />
            <X.P>
                `1``3``3`
            </X.P>
            <X.H1>pytorch</X.H1>
            <X.P>pytorch`topk`</X.P>
            <X.CodeBlock
                language="python"
                code={`
                val,idx=torch.topk(tsr,k=3,dim=1)

                print(val)
                # tensor([[8, 7, 5],
                #         [9, 8, 4],
                #         [8, 6, 5],
                #         [6, 5, 4]])

                print(idx)
                # tensor([[4, 3, 2],
                #         [0, 3, 4],
                #         [0, 4, 1],
                #         [3, 4, 2]])
                `}
            />
            <X.P>`val``idx`</X.P>
            <X.H1>numpy</X.H1>
            <X.P>numpy`topk`</X.P>
            <X.P>
                `numpy.argpartition``kth`---
                
            </X.P>
            <X.P>`numpy.argpartition``arr`</X.P>
            <X.CodeBlock
                language="python"
                code={`
                idx=numpy.argpartition(-arr,kth=3,axis=1)
                print(idx)
                # [[3 4 2 1 0]
                #  [3 0 4 2 1]
                #  [0 4 1 3 2]
                #  [3 4 2 0 1]]
                `}
            />
            <X.P>`k`</X.P>
            <X.CodeBlock
                language="python"
                code={`
                idx=idx.take(indices=range(3),axis=1)
                print(idx)
                # [[3 4 2]
                #  [3 0 4]
                #  [0 4 1]
                #  [3 4 2]]
                `}
            />
            <X.P>`numpy.take_along_axis``idx`</X.P>
            <X.CodeBlock
                language="python"
                code={`
                val=numpy.take_along_axis(arr,indices=idx,axis=1)
                print(val)
                # [[7 8 5]
                #  [8 9 4]
                #  [8 6 5]
                #  [6 5 4]]
                `}
            />
            <X.P>**`numpy.argsort`</X.P>
            <X.CodeBlock
                language="python"
                code={`
                sorted_idx=numpy.argsort(-val,axis=1)
                idx=numpy.take_along_axis(idx,indices=sorted_idx,axis=1)
                val=numpy.take_along_axis(val,indices=sorted_idx,axis=1)

                print(val)
                # [[8 7 5]
                #  [9 8 4]
                #  [8 6 5]
                #  [6 5 4]]

                print(idx)
                # [[4 3 2]
                #  [0 3 4]
                #  [0 4 1]
                #  [3 4 2]]
                `}
            />
            <X.H1></X.H1>
            <X.P>numpytorch`topk`</X.P>
            <X.CodeBlock
                language="python"
                code={`
                def topk_numpy(arr,k,dim):
                    idx=numpy.argpartition(-arr,kth=k,axis=dim)
                    idx=idx.take(indices=range(k),axis=dim)
                    val=numpy.take_along_axis(arr,indices=idx,axis=dim)
                    sorted_idx=numpy.argsort(-val,axis=dim)
                    idx=numpy.take_along_axis(idx,indices=sorted_idx,axis=dim)
                    val=numpy.take_along_axis(val,indices=sorted_idx,axis=dim)
                    return val,idx
                `}
            />
            <X.HighlightBlock>
                <X.P>torch`topk`~</X.P>
            </X.HighlightBlock>
            <X.P></X.P>
            <X.CodeBlock
                language="python"
                code={`
                bigarr=numpy.random.rand(64,16,24,24)
                bigtsr=torch.tensor(bigarr)

                val_t,idx_t=torch.topk(bigtsr,k=7,dim=1)
                val_n,idx_n=topk_numpy(bigarr,k=7,dim=1)

                print(val_n.shape) #(64, 7, 24, 24)
                print(numpy.all(val_t.numpy()==val_n)) #True
                print(numpy.all(idx_t.numpy()==idx_n)) #True
                `}
            />
            <X.H1></X.H1>
            <X.P>**`argpartition``argsort`</X.P>
            <X.P>
                `argsort``argpartition``argpartition`---
                `topk``k``dim`
            </X.P>
        </>
    );
}
