import X from 'src/component/X';

export default function Blog({title}) {
    return (
        <>
            <X.Title>{title}</X.Title>
            <X.P>
                @A Closer Look at Spatiotemporal Convolutions for Action
                Recognition[https://arxiv.org/pdf/1711.11248.pdf]@---
                R(2+1)DR3D3D2D+1D
            </X.P>
            <X.P>
                PyTorchGithub @Github:
                R2Plus1D-PyTorch[https://github.com/irhum/R2Plus1D-PyTorch]@
            </X.P>
            <X.H1>(2+1)D</X.H1>
            <X.P>
                3D$(C_i,C_o,K_t,K_w,K_h)$$C_i,C_o$$K_t,K_w,K_h$3D\n
                R(2+1)D`Conv3d``SpatioTemporalConv`
            </X.P>
            <X.Image src={require('./fig1.jpg')} width="100%" invertInDarkTheme />
            <X.P noMarginBottom>
                `SpatioTemporalConv``Conv3d``5`
            </X.P>
            <X.Uli>$(C_i,C,1,K_w,K_h)$`Conv3d`</X.Uli>
            <X.Uli>`BatchNorm`+`ReLU`</X.Uli>
            <X.Uli>$(C,C_o,K_t,1,1)$`Conv3d`</X.Uli>
            <X.P withMarginTop>`shape`</X.P>
            <X.CodeBlock
                language="python"
                code={`
                import torch
                from r2plus1d.module import SpatioTemporalConv

                video_3d=torch.rand(8,3,60,32,32) #(batch*channels*frames*w*h)
                conv3d=torch.nn.Conv3d(in_channels=3,out_channels=16,kernel_size=[7,5,5])
                spconv=SpatioTemporalConv(in_channels=3,out_channels=16,kernel_size=[7,5,5])

                output1=conv3d(video_3d)
                output2=spconv(video_3d)

                print(output1.shape) #torch.Size([8, 16, 54, 28, 28])
                print(output2.shape) #torch.Size([8, 16, 54, 28, 28])
                `}
            />
            <X.P>$C$$C$</X.P>
            <X.Formula text="C=C_i C_oK_tK_wK_h/(C_iK_wK_h+C_oK_t)" />
            <X.P noMarginBottom>
                R(2+1)DR3D---
                R(2+1)D
            </X.P>
            <X.Oli></X.Oli>
            <X.Oli>R(2+1)D</X.Oli>
            <X.Image src={require('./fig2.jpg')} width="600" invertInDarkTheme />
        </>
    );
}
